.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
/* right side */
.w-rightm {
    position: relative;
}

.w-mainCircle {
    left: 18rem;
    position: relative;
    width: 18rem;
    height: 16rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: -15rem;
    background: white;
}

.w-mainCircle>*{
    position: absolute;
}
.w-mainCircle > :nth-child(1) {
    top: -3rem;
    left: 6rem;
}
.w-mainCircle > :nth-child(2) {
    left: -3rem;
    top: 5rem;
}
.w-mainCircle > :nth-child(3) {
    left: 6rem;
    top: 5rem;
}
.w-mainCircle > :nth-child(4) {
    left: 15rem;
    top: 5rem;
}
.w-mainCircle > :nth-child(5) {
    left:  6rem;
    top: 13rem;
}

w.-mainCircle{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
    border-radius: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: 'none';
    box-shadow: var(--smboxShadow);
    background: white;
}

.w-secCircle > img {
    transform: scale(0.6);
}

.w-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.blueCircle{
  background: #1949B8;
  left: 30rem;
  top: 103rem;
}
.yellowCircle{
    background: #F5C32C;
    left: 30rem;
    top: 108rem;
}







